}

.footer-flex-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
}
@media (max-width: 1023px) {
  .footer-flex-list {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 767px) {
  .footer-flex-list {
    display: none;
  }
}

.footer-flex-list li {
  width: 220px;
}

.footer-section-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  color: #f2f5f7;
}
@media (max-width: 767px) {
  .footer-section-list {
    display: none;
  }
}

.section-item {
  margin-bottom: 10px;
}

.section-item__link {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #f2f5f7;
}

.about {
  padding-bottom: 120px;
  background-color: #e5e5e5;
}
@media (max-width: 1023px) {
  .about {
    padding-bottom: 80px;
  }
}

.about__wrapper {
  display: grid;
  grid-template-columns: 1fr 4fr;
  gap: 100px;
}
@media (max-width: 767px) {
  .about__wrapper {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 767px) {
  .about__wrapper img {
    display: none;
  }
}

.about__wrapper h1 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin: 0;
  padding: 0;
  margin-bottom: 15px;
}

.about__wrapper p {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 20px;
}

.about__wrapper a {
  border: none;
  cursor: pointer;
  border-radius: 3px;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid #00b2ff;
  padding: 14px;
  color: #00b2ff;
  width: 380px;
}

